}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.loader {
  position: fixed;
  top: 50%;
  left: 40%;
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #ef4444;
  border-right: 16px solid #fecaca;
  border-bottom: 16px solid #ef4444;
  border-left: 16px solid #fecaca;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
.heading {
  font-size: 30px;
  font-family: sans-serif;
  font-weight: 700;
  color: #ef4444;
  text-transform: uppercase;
  word-spacing: 3pt;
