  z-index: 1;
}

#menu1 {
  right: 2%;
  top: 34%;
  position: fixed;
  z-index: 1;
}

#bottomNav {
  display: none;
}
.glyph {
  font-size: 40px;
}

.text-danger {
  font-size: 12px;
}
.haks {
  height: 40rem;
  width: 60rem;
}
.autob {
  height: 9rem;
  width: 9rem;
}

.card {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px #47484c;
  margin-top: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
}
.card-img-top {
  width: 100%;
  height: 18rem;
}
.brandImg {
  height: 60px;
  width: 79px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  border: 1px solid #ef4444;
}
.AutoPartbrandImg {
  height: 15rem;
  width: 15rem;
}
.brandCol {
  grid-row-start: 1;
  grid-row-end: 3;
}
.bottomNav {
  display: none;
}
.item {
  margin-right: 5px;
  margin-left: 5px;
}

.item-img {
  height: 30vh;
  width: 100%;
}
.social-icons-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: auto;
          flex: auto;
  -webkit-justify-content: center;
          justify-content: center;
}
.social-icons {
  color: #ef4444;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 50px;
  margin: 1rem;
  padding: 1rem;
  box-shadow: 5px 5px 20px #fecaca;
}
.social-icons :hover {
  background-color: #fecaca;
  box-shadow: 5px 5px 20px #fecaca;
  transition: background 0.2s;
  border-radius: 3px;
}
.vert .carousel-inner .carousel-item {
  height: 100px;
}
.vert .carousel-inner {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: radial-gradient(
    farthest-corner at 40px 40px,
    #111 20%,
    #333 100%,
    #ddd 100%
  );
  color: white;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 32px;
  margin: 1rem;
  padding: 1rem;
  box-shadow: 5px 5px 20px silver;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  width: 100%;
  border-radius: 5px;
}
.auto_parts_button {
  display: none;
}
.header-paragraph-carousel {
  padding-top: 7rem;
}
.mj-heading {
  font-size: 60px;
}
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 10px;
}
.grid-item {
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 30px;
  text-align: center;
  border: 3px solid black;
  border-radius: 5px;
  margin: 5px;
}
@media (max-width: 992px) {
  .auto_parts_button {
    display: none;
  }
}
@media (min-width: 768px) {
