    font-size: 15px;
  }
  .heading {
    font-size: 28px;
  }
  #botNav {
    display: none;
  }
  #navMob {
    display: none;
  }
  .paragraph {
    font-weight: 300;
    font-size: 16px;
  }
  .glyph {
    font-size: 35px;
  }
  footer {
    position: relative;
    bottom: 0;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .card {
    margin-left: 0;
  }
  .welcomeImg {
    height: 80px;
    width: 80px;
  }
  .header-car-img {
    padding-top: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .vert .carousel-inner .carousel-item {
    padding: 1rem;
    width: 150px;
    padding-left: 4rem;
  }
  .vert .carousel-inner {
    width: 210px;
    height: 100px;
    font-size: 16px;
    font-weight: 500;
  }
  .auto_parts_button {
    display: none;
  }
  .header-paragraph-carousel {
    padding-left: 2rem;
  }
  .mj-heading {
    font-size: 24px;
  }
  .AutoPartbrandImg {
    height: 10rem;
    width: 10rem;
  }
}
@media (max-width: 576px) {
  .header-car-img {
    padding-top: 1rem;
    margin-left: -2rem;
    height: 12em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .paragraph {
    font-size: 16px;
  }
  ::-webkit-input-placeholder {
    font-size: 16px;
  }
  :-ms-input-placeholder {
    font-size: 16px;
  }
  ::placeholder {
    font-size: 16px;
  }
  .small {
    font-size: 15px;
  }
  .nav1 {
    font-size: 20px;
  }
  .heading {
    font-size: 23px;
  }
  .btn-primary {
    font-size: 14px;
    width: 10rem;
    height: 2.6rem;
    text-shadow: none;
    margin: 3px;
  }
  .flex-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
