  }
  #menu1 {
    display: none;
  }
  .fixed-bottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1030;
    background-color: #ef4444;
  }
  .bottomNav {
    display: block;
    color: white;
  }
  .bottomNav :hover {
    color: #fecaca;
  }
  .bottomNav :active {
    color: #fecaca;
  }
  .batteryProd {
    height: 50vh;
    width: 50vh;
  }
  footer {
    position: relative;
    padding-bottom: 150px;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .glyph {
    font-size: 30px;
  }
  .card {
    margin-left: 0;
  }
  .welcomeImg {
    height: 50px;
    width: 50px;
  }
  .vertical-center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
  }
  .mobile-bottom-navbar {
    font-size: 16px;
    font-family: "EB Garamond", sans-serif;
    margin: 1em;
  }
  .header-paragraph-carousel {
    position: relative;
    padding-top: 4rem;
    padding-left: 1rem;
  }
  .vert .carousel-inner .carousel-item {
    padding: none;
    margin: none;
    width: 150px;
    height: 100px;
    padding-left: 20px;
  }
  .vert .carousel-inner {
    width: 150px;
    height: 90px;
    font-size: 16px;
    font-weight: 500;
    padding-left: 20px;
  }
  .auto_parts_button {
    display: block;
    right: 2%;
    top: 2rem;
    position: fixed;
    z-index: 1;
  }
  .mj-heading {
    font-size: 32px;
  }
  .loader {
    position: fixed;
    margin: auto;
    width: 50%;
    text-align: center;
    border: 10px solid #f3f3f3;
    border-radius: 50%;
    border-top: 10px solid #ef4444;
    border-right: 10px solid #fecaca;
    border-bottom: 10px solid #ef4444;
    border-left: 10px solid #fecaca;
    width: 70px;
    height: 70px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
  }
  .AutoPartbrandImg {
    height: 8rem;
    width: 8rem;
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes mymove {
  from {
    left: 0;
  }
  to {
    left: 200px;
  }
}
@keyframes mymove {
  from {
    left: 0;
  }
  to {
    left: 200px;
  }
}

